.user-collection-heading {
  margin-top: 25px;
  margin-bottom: 0;
}

#user-list {
  flex-flow: wrap;
  justify-content: space-around;
  align-items: center;
  width: 60vw;
  margin-bottom: 0;
  display: flex;
}

.user-details-container {
  background-color: #fff;
  border: 1px solid #000;
  width: 175px;
  height: 125px;
  margin: 25px 15px 50px;
  padding: 5%;
  transition: box-shadow .3s, transform .3s;
  box-shadow: 5px 5px #87878780;
}

.user-details-container:hover {
  transform: scale(1.1);
  box-shadow: 5px 5px #6d006db3;
}

@media screen and (width <= 768px) {
  #user-list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    display: flex;
  }

  .user-details-container {
    width: 50vw;
    height: 15vh;
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 1.25em;
  }

  .user-details-container > h3 {
    margin-top: .5vh;
    margin-bottom: 3vh;
  }
}
